body {
  background: linear-gradient(45deg, #4158d0 0%, #91BEF3 100%);
  margin: 0;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Inter;
  width: 100vw;
  min-height: 100vh;
  color: white;
  overflow: hidden;
}

@media (max-width: 770px) {
  .app {
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.header {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  height: 48px;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 24px;
  margin-bottom: 24px;
}

@media (max-width: 770px) {
  .header {
    font-size: 20px;
  }
}

@media (max-width: 576px) {
  .header p{
    display: none;
  }
}


.headercontainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1280px;
  padding: 0 20px 0 20px;
}

@media (max-width: 770px) {
  .headercontainer {
    width: 98%;
    justify-content: space-between;
  }
}

.main {
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
  flex: 1;
  min-width: 375px;
  max-width: 1440px;
}

.left-column {
  display: flex;
  flex-wrap: wrap;
  flex: 0 1 680px;
  justify-content: flex-start;
  width: 60%;
  margin: 0 0 0 40px;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}

@media (max-width: 1200px) {
  .left-column {
    margin: 0 0 0 20px;
    flex: 0 1 620px;
  }
}

@media (max-width: 1024px) {
  .left-column {
    margin: 0 0 0 10px;
    border: 0;
  }
}

.right-column {
  display: flex;
  flex-wrap: wrap;
  flex: 0 1 560px;
  justify-content: center;
  align-content: center;
  width: 40%;
  gap: 32px;
}

@media (max-width: 1200px) {
  .right-column {
    flex: 0 1 480px;
    justify-content: flex-start;
    margin-left: 10px;
  }
}

@media (max-width: 1200px) {
  .right-column .sunrise, .sunset {
    width: 200px;
    height: 400px;
  }
}

.right-column .sunrise, .sunset {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  width: 220px;
  height: 440px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 100px;
  font-size: 72px;
  font-weight: 100;
}

@media (max-width: 1200px) {
  .right-column .sunrise, .sunset {
    width: 200px;
    height: 400px;
    font-size: 48px;
  }
}

@media (max-width: 990px) {
  .right-column .sunrise, .sunset {
    width: 160px;
    height: 320px;
    font-size: 48px;
  }
}

.sunrise p, .sunset p {
  margin: 0;
}

.sunrise p:nth-of-type(1), .sunset p:nth-of-type(1) {
  font-size: 48px;
}

@media (max-width: 770px) {
  .sunrise p:nth-of-type(1), .sunset p:nth-of-type(1) {
    font-size: 32px;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.geo {
  width: 400px;
  display: flex;
  flex-wrap: nowrap;
  align-items: baseline;
}

.geo img {
  width: 32px;
  height: 32px;
  margin-right: 8px;
}

.temp-container {
  width: 400px;
  height: 320px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.weatherIcon {
  width: 120px;
  height: 84px;
}

.cityName {
  font-weight: 300;
  line-height: 120%;
  font-size: 40px;
  margin: 0 0 16px 0;
}

.temp-group {
  width: 400px;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.current-data {
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 120%;
  margin: 0 0 16px 0;
}

.temp {
  font-weight: 100;
  line-height: 120%;
  font-size: 80px;
  margin: 0;
}

sup {
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
  letter-spacing: -1.6px;
}

.skyState {
  display: flex;
  align-items: center;
  min-width: 320px
}

.skyState p {
  font-size: 24px;
  font-weight: 300;
  margin: 0;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  width: 600px;
  justify-content: flex-start;
  column-gap: 16px;
}

@media (max-width: 1024px) {
  .cards {
    padding-bottom: 32px;
    margin-bottom: 32px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  }
}

.card {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  flex: 0 1 110px;
  justify-content: space-around;
  align-items: center;
  max-width: 110px;
  height: 200px;
  font-size: 24px;
  font-weight: 400;
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px solid #fff;
  border-radius: 16px;
  padding: 16px 24px 16px 24px;
  box-sizing: border-box;
  margin-bottom: 8px;
}

.card:hover {
  position: relative;
  bottom: 4px;
}

.card img {
  width: 80px;
  height: 54px;
  margin: 0;
  padding: 0;
}


.additional-panel {
  display: flex;
  flex-wrap: nowrap;
  column-gap: 32px;
  width: 600px;
  margin-bottom: 16px;
}

.wind-group, .humidity-group {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  font-size: 22px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.additional-panel img {
  width: 22px;
  height: 22px;
  margin-right: 4px;
}

input {
  width: 160px;
  height: 32px;
  top: 20px;
  right: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px solid #fff;
  border-radius: 10px;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  height: 64px;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 20px;
  font-weight: 300;
  margin-top: 40px;
}